/* -------------------------------------------------- */
/* Global Resets & Body Background                    */
/* -------------------------------------------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Helvetica Neue", Arial, sans-serif;
}

body {
  background-image: linear-gradient(
    to right bottom,
    #f9dfbe,
    #ffe1d0,
    #ffe6e3,
    #ffedf3,
    #fff5fc,
    #fdeefa,
    #fbe8f8,
    #f9e1f6,
    #ffc8dd,
    #ffb0b4,
    #fd9e80,
    #df974a
  );
  background-size: cover;
}

/* -------------------------------------------------- */
/* Hearts & Money Confetti                            */
/* -------------------------------------------------- */
.hearts-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 9999;
}

.heart {
  position: fixed;
  font-size: 2rem;
  transform: translateX(-50%) translateY(100vh);
  animation-name: floatUp;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}
@keyframes floatUp {
  0% {
    transform: translateX(-50%) translateY(100vh) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(-50%) translateY(-100vh) scale(2);
    opacity: 0;
  }
}

/* -------------------------------------------------- */
/* App Container                                      */
/* -------------------------------------------------- */
.app-container {
  width: 100%;
  margin: 0 auto;
}

/* -------------------------------------------------- */
/* Sticky Top Menu + Hamburger                        */
/* -------------------------------------------------- */
.top-menu {
  position: sticky;
  top: 0;
  z-index: 999;
  background-color: transparent;
  transition: background-color 0.3s, box-shadow 0.3s;
  padding: 2rem 0;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
}
.top-menu.scrolled {
  background-color: rgba(255,255,255,0.9);
  border-radius: 2rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}
.top-menu nav { position: relative; }

.nav-links {
  list-style: none;
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
}

.hamburger {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  flex-direction: column;
  gap: 0.3rem;
  position: absolute;
  top: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
}
.hamburger .bar {
  width: 25px;
  height: 3px;
  background-color: #233830;
}

.top-menu nav ul li a {
  padding: 0.5rem 3rem;
  border-radius: 1rem;
  text-decoration: none;
  color: #233830;
  transition: background-color 0.3s ease;
}
.top-menu nav ul li a:hover {
  background-color: #efbc79;
}
.top-menu nav ul li a.active {
  background-color: #fdf3e6;
}

.close-menu-item {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: none;
}
.close-menu-btn {
  background: none;
  border: none;
  font-size: 2rem;
  color: #333;
  cursor: pointer;
}
.close-menu-btn:hover { color: #e5a152; }

@media (max-width: 1024px) {
  .nav-links {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,0.4);
    backdrop-filter: blur(10px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    transform: translateY(-100%);
    display: none;
    z-index: 9999;
  }
  .nav-links.open { display: flex; animation: slideDown 0.5s forwards; }
  .hamburger { display: flex; }
  .close-menu-item { display: block; }
}
@keyframes slideDown { from {transform: translateY(-100%);} to{transform: translateY(0);} }

/* -------------------------------------------------- */
/* Content Wrapper & Two-Column Rows                  */
/* -------------------------------------------------- */
.content-wrapper {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}
.two-column-row {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 3rem;
  margin-bottom: 2rem;
}

/* -------------------------------------------------- */
/* Hero Section                                       */
/* -------------------------------------------------- */
.hero { text-align: center; padding: 2rem 0; }
.profile-image {
  width: 360px; height: 360px;
  border-radius: 50%; object-fit: cover;
  margin-bottom: 1rem; cursor: pointer;
  animation: pulse 2s infinite;
}
@keyframes pulse { 0%{scale:1;}50%{scale:1.03;}100%{scale:1;} }
.profile-name {
  font-size: clamp(1.5rem,4vw,8rem);
  font-weight:700; line-height:1.1;
  margin:1rem 0; text-align:center;
}
.profile-title {
  font-size: clamp(1.5rem,3vw,6rem);
  font-weight:600; color:#e5a152;
  text-align:center; margin-bottom:1rem;
}

/* -------------------------------------------------- */
/* How I Help Companies                               */
/* -------------------------------------------------- */
.help-section {
  background: #fff5fc;
  border-radius: 1rem;
  padding: 2rem 1.5rem;
  margin: 2rem 0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}
.help-section h2 {
  text-align: center;
  font-size: 1.6rem;
  margin-bottom: 1rem;
}
.help-section ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}
.help-section ul li {
  background: #ffcae9;
  padding: 0.8rem 1.2rem;
  border-radius: 9999px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.1);
}

/* -------------------------------------------------- */
/* Section Containers                                 */
/* -------------------------------------------------- */
.section-container {
  background-color: #fff;
  border-radius: 1rem;
  padding: 3rem;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
  margin-bottom: 2rem;
}
.section-container h2 {
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 3rem;
  color: #2c3e50;
}

/* -------------------------------------------------- */
/* Projects Grid                                      */
/* -------------------------------------------------- */
.projects-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
}
.project-card {
  background-color: #f8f7f5;
  padding: 3rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 5px rgba(0,0,0,0.05);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.project-card:hover {
  transform: scale(1.02);
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}
.project-image {
  width: 100%;
  border-radius: 0.3rem;
  margin-bottom: 1rem;
}
.project-title {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}
.project-description {
  font-size: 1rem;
  margin-bottom: 0.7rem;
  color: #333;
}
.project-link {
  background: none;
  border: none;
  color: #2b6cb0;
  cursor: pointer;
  text-decoration: underline;
  transition: color 0.3s ease;
  font-size: small;
}
.project-link:hover {
  color: #1a4c7b;
}

/* -------------------------------------------------- */
/* Case Study iframe container                        */
/* -------------------------------------------------- */
.iframe-container {
  position: relative;
  width: 100%;
  padding-top: 65.7%;
  margin-bottom: 1rem;
}
.iframe-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

/* -------------------------------------------------- */
/* Experience & Education                             */
/* -------------------------------------------------- */
.jobs-container,
.edu-container {
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
}
.experience-card,
.education-card {
  background-color: #f8f7f5;
  border-radius: 0.5rem;
  padding: 1rem;
  margin-bottom: 2rem;
  box-shadow: 0 0 5px rgba(0,0,0,0.05);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.experience-card:hover,
.education-card:hover {
  transform: scale(1.02);
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}
.experience-top,
.education-top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.experience-description,
.education-description {
  margin-top: 1rem;
  color: #333;
  text-align: justify;
}
.experience-left h3,
.education-left h3 {
  font-size: 1.3rem;
  margin-bottom: 0.3rem;
}
.experience-left .company,
.education-left .company {
  color: #666;
  font-weight: bold;
  margin-bottom: 0.5rem;
}
.experience-dates,
.education-dates {
  color: #666;
  white-space: nowrap;
}

/* -------------------------------------------------- */
/* Skills & Tools                                     */
/* -------------------------------------------------- */
.skills-list,
.tools-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.8rem;
  list-style: none;
}
.skills-list li,
.tools-list li {
  background-color: #f8f7f5;
  padding: 0.4rem 0.7rem;
  border-radius: 0.3rem;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.skills-list li:hover,
.tools-list li:hover {
  transform: scale(1.02);
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

/* -------------------------------------------------- */
/* Footer                                             */
/* -------------------------------------------------- */
.footer {
  padding: 6rem 0;
  border-top: 1px solid #ddd;
  text-align: center;
  color: #233830;
  font-size: 1.2rem;
}
.footer .footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}
.footer p {
  margin: 0.5rem 0;
}
.footer .social-links a {
  margin: 0 0.5rem;
  transition: transform 0.3s ease;
}
.footer .social-links a:hover {
  transform: scale(1.1);
}
.linkedin-icon {
  width: 40px;
}

/* -------------------------------------------------- */
/* Responsive Adjustments                             */
/* -------------------------------------------------- */
@media (max-width: 768px) {
  .two-column-row { flex-direction: column; }
  .content-wrapper { padding: 0 1rem; }
  .projects-grid { grid-template-columns: 1fr; gap: 1rem; }
  .project-card { padding: 1rem; }
  .experience-card,
  .education-card { padding: 1rem; }
  .experience-top,
  .education-top { flex-direction: column; }
  .experience-dates,
  .education-dates { margin: 0.5rem 0 1rem; }
  .nav-links li a { font-size: 1.2rem; }
  .profile-image { width: 280px; height: 280px; }
  .profile-name { font-size: 2rem; }
  .profile-title { font-size: 1.8rem; }
}

/* ───────── Work-in-Progress Banner ───────── */
.banner {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #ffc8dd;         /* static pink bar */
  height: 2.5rem;              /* adjust to taste */
  display: flex;
  align-items: center;
  overflow: hidden;
  z-index: 1000;               /* sit above content but below your menu */
}

/* the scrolling text inside the pink bar */
.banner-text {
  display: inline-block;
  padding-left: 100%;          /* start fully off-right */
  white-space: nowrap;
  animation: marquee 16s linear infinite;
  font-style: italic;
  font-weight: bold;
}

@keyframes marquee {
  0%   { transform: translateX(0); }
  100% { transform: translateX(-100%); }
}

/* push your sticky menu down by the banner’s height */
.top-menu {
  position: sticky;
  top: 2.5rem;                 /* same as .banner height */
  z-index: 1100;               /* above the banner */
  /* …all your existing .top-menu rules… */
}

.profile-image-wrapper {
  position: relative;
  display: inline-block;
}

.image-hint {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1rem;
  background: rgba(255,255,255,0.7);
  color: #333;
  padding: .4rem .8rem;
  border-radius: 50px;
  pointer-events: none;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  animation: bubblePulse 3s ease-in-out infinite;
}

@keyframes bubblePulse {
  0%   { transform: translateX(-50%) scale(1); opacity: .9; }
  50%  { transform: translateX(-50%) scale(1.1); opacity: 1;  }
  100% { transform: translateX(-50%) scale(1); opacity: .9; }
}